export  function roundPointCalculator(game,roundIndex, playerIndex){

    if(game['call'][roundIndex] === undefined || !game['call'][roundIndex][playerIndex] ){
        return '';
    }

    let call = parseInt(game['call'][roundIndex][playerIndex]);

    if(game['result'][roundIndex] === undefined || !game['result'][roundIndex][playerIndex] ){
        return call;
    }
    let result = parseInt(game['result'][roundIndex][playerIndex]);
    
    if(result < call) {
        return '-' + call;
    }
    else if(call === result ){
        return '' + call.toString() + '.0';
    }else{
        return '' + call.toString() + '.' + (result-call);
    }
}

export function calculateResult(game){
    let 
    pSum =[0.0,0.0,0.0,0.0];
    let c,r;
    
    game['call'].forEach((round,index) =>{

      game['call'][index].forEach((item,cIndex) => {
         c = game['call'][index][cIndex];
         r = game['result'][index][cIndex];

         if(!c){
           pSum[cIndex] = pSum[cIndex] + 0.0;
         }
         else if(!r){
           pSum[cIndex] = pSum[cIndex] + parseFloat(c);
         }
         else{
           pSum[cIndex] = getSum(pSum[cIndex],calculate(parseFloat(c),parseFloat(r)));
         }
      });
    });

    return pSum;
     
  }

  function getSum(sum, p){

    let sumNumber = sum < 0 ? Math.floor(sum) : Math.floor(sum);
    let sumDecimal = Math.abs(sum) - Math.abs(Math.floor(sum));

    let pNumber = p < 0 ?  Math.floor(p) : Math.floor(p);
    let pDecimal = Math.abs(p) - Math.abs(Math.floor(p));

    let x = sumNumber + pNumber;

    let totalDecimal = sumDecimal + pDecimal;

    
    if(totalDecimal > 1){
      x = x + 1 ;
      totalDecimal = totalDecimal - 1;
    }
    if(x < 0 ) {
      return -1 * (Math.abs(x) + totalDecimal);
    }else {
      return x + totalDecimal;
    }

  }

  function calculate (call, result) {
    let sum = 0.0;
    if(result < call) {
      sum =  - 1 * call;
    }
    else if(call === result ){
      sum = call;
    }else{
      sum = call +  (result-call)/10;
    }
    return sum;
  }