import React from 'react';
import logo from './logo.svg';
import NewGame from './NewGame'
import './App.css';
import {roundPointCalculator,calculateResult} from './Calculator.js';

export default class App extends React.Component {

  constructor(props){
    super(props);
    let games = localStorage.getItem('games') ? Array.from(JSON.parse(localStorage.getItem('games'))) : [];
    let currentGameId = localStorage.getItem('currentGameId');
    currentGameId = currentGameId ? Number(currentGameId) : null;
    let currentGame = null;
    games.forEach((game,index) => {
      if(game.gameId === currentGameId){
        currentGame = game;
      }
    });
    
    this.state = {games:games,currentGame: currentGame};
  }

  render () {

    return (
    <div className="App">
      <header className="App-header">
        <h2>Call Break Counter </h2>
      </header>
      <div className="container body-container">
        <div className="row current-game">
          {
            <NewGame key={this.state.game?this.state.game.gameId:Date.now()} onSave={(game)=>this.saveGame(game)} onClearGameId = {(currentGame)=>this.clearGameId(currentGame)} game={this.state.currentGame}/>
          }
        </div>
        <div className="row">
          { 
            this.getHistory()
          }
        </div>
      </div>
      </div>
    )
  }

  clearGameId(currentGame){
    this.setState({currentGame:null});
    localStorage.removeItem('currentGameId');
  }

  setCurrentGameId(gameId){
    let currentGame = null;
    this.state.games.forEach((game,gameIndex) => {
      if(game.gameId === gameId){
        currentGame = game;
      }
    });
    this.setState({currentGame: currentGame});
    this.localStorage.setItem('currentGameId',gameId);
  }

  getHistory(){
    let allGames = JSON.parse(JSON.stringify(this.state.games));
    let allGamesResult = [];
    let curentGameIndex = null;
    
    allGames.forEach((game,index) => {
      
      if(this.state.currentGame && game.gameId === this.state.currentGame.gameId){
        curentGameIndex = index;
      }
        allGamesResult[index] = calculateResult(game);
    });
    if(curentGameIndex !==null){
      allGames.splice(curentGameIndex,1);
      allGamesResult.splice(curentGameIndex,1);
    }

    if(allGames.length){
      return (
       allGames.map(( game, gameIndex ) => (
          <table key={gameIndex} className="table table-dark">
            <thead>
              <tr>
                <th scope="col">Round</th>
                <th scope="col">{game['players'][0]}</th>
                <th scope="col">{game['players'][1]}</th>
                <th scope="col">{game['players'][2]}</th>
                <th scope="col">{game['players'][3]}</th>
                <th scope="col">{this.gameStatusBtn(game)}</th>
              </tr>
            </thead>
            <tbody>
               { this.getTableRow(game)}
            </tbody>
            <tfoot>
              <tr>
              <td>Result</td>
              <td>{allGamesResult[gameIndex][0].toFixed(1)}</td>
              <td>{allGamesResult[gameIndex][1].toFixed(1)}</td>
              <td>{allGamesResult[gameIndex][2].toFixed(1)}</td>
              <td>{allGamesResult[gameIndex][3].toFixed(1)}</td>
              <td></td>
              </tr>
            </tfoot>
          </table>
        ))  
      )
    }

  }

  gameStatusBtn(game){
    let gameComplete = true;
    if(game['call'].length === 5){
      game['call'].forEach((call,index) => {
      game['call'][index].forEach((round,playerIndex) => {
        if(game['call'][index][playerIndex] === null || game['result'][index][playerIndex] === null){
          gameComplete = false;
        }
      });
      });
    }
    else{
      gameComplete = false;
    }

    if(gameComplete){
      return ''
    }
    else{
      if(this.state.currentGame ==null){
        return <input type="button" onClick={()=>this.finishGame(game.gameId)} value="Finish"/>;
      }
      else{
        return '';
      }
      
    }
    
  }

  finishGame(gameId){
    let games = this.state.games;
    let currentGame = this.state.game;
    games.forEach((game,index) => {
      if(game.gameId === gameId){
        currentGame = game;
      }
    });
    this.setState({currentGame: currentGame});
    localStorage.setItem('currentGameId',gameId);
  }

  saveGame(saveGame){

    const gameId = saveGame.gameId;
    let saveGameIndex = null;
    let games = JSON.parse(JSON.stringify(this.state.games));
    games.forEach((game,index) => {
      if(game.gameId === gameId){
        console.log('save gave index found',index,game.gameId);
        saveGameIndex = index;
      }
    });
    if(saveGameIndex !== null){
      games[saveGameIndex] = saveGame;
    }
    else{
      games.unshift(saveGame);
    }
    if(games.length > 10){
      games.slice(0,10);
    }
       
    this.setState({games:games,currentGame:saveGame});
    localStorage.setItem('games',JSON.stringify(games));
    localStorage.setItem('currentGameId',saveGame.gameId);
    
  }

  getTableRow(game){
      return (
        game['call'].map(( round, roundIndex ) => (
          <tr key={roundIndex}>
            <td>{roundIndex+1}</td>
            <td>{ roundPointCalculator(game,roundIndex,0)}</td>
            <td>{ roundPointCalculator(game,roundIndex,1)}</td>
            <td>{ roundPointCalculator(game,roundIndex,2)}</td>
            <td>{ roundPointCalculator(game,roundIndex,3)}</td>
          </tr>
        ))
      )
  }

}
